


.article-button {
    height: 50px;
    width: 150px;
    border-radius: 50px; /* For elongated, pill-shaped buttons */
    background-color:gold;
    cursor: pointer;
}
.article-button:hover {
    background-color:rgba(255, 217, 0, 0.637);
    box-shadow: 0 6px 8px rgba(0, 0, 0, 0.2);
}